/* CustomDatePicker.css */
.custom-date-picker-container {
  width: 100%;
  margin-bottom: 16px;
}

.custom-date-picker {
  width: 100%;
  max-width: 100%;
}

/* Input field styling */
.custom-date-picker [data-slot="input"] {
  width: 100%;
  padding: 10px 12px;
  font-size: 14px;
  border: 1px solid #e2e8f0;
  border-radius: 0.5rem;
  background-color: white;
  transition: all 0.2s;
}

.custom-date-picker [data-slot="input"]:focus-within {
  border-color: #4f46e5;
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.2);
}

/* Calendar styling */
.custom-date-picker [data-slot="calendar"] {
  border: 1px solid #e2e8f0;
  border-radius: 0.5rem;
  background-color: white;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}

/* Date cell styling */
.custom-date-picker [data-slot="calendar"] [role="gridcell"] {
  text-align: center;
  padding: 8px;
}

/* Selected date styling */
.custom-date-picker [data-slot="calendar"] [aria-selected="true"] {
  background-color: #4f46e5;
  color: white;
  border-radius: 50%;
}

/* Today's date styling */
.custom-date-picker [data-slot="calendar"] [data-today="true"] {
  font-weight: bold;
  border: 1px dashed #4f46e5;
}

/* Month navigation buttons */
.custom-date-picker [data-slot="calendar"] button {
  background-color: transparent;
  border: none;
  padding: 4px 8px;
  cursor: pointer;
  color: #4f46e5;
}

.custom-date-picker [data-slot="calendar"] button:hover {
  background-color: #f3f4f6;
  border-radius: 4px;
}